input {
  width: 300px;
  margin-bottom: 20px;
}
 .range {
  -webkit-appearance: none;
  background-color: rgb(253, 201, 233);
  height: 2px;
  width: 300px;
  cursor: pointer;
  opacity: 1;
  margin-bottom: 20px;
}
 .range::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 15px;
  width: 15px;
  background-color: rgb(192, 20, 106);
  cursor: pointer;
  border-radius: 50%;
} 

 